<template>
  <v-app id="starTerk" >
    <v-navigation-drawer 
      permanent
      absolute
      overflow
      app
      stateless
    >

      <v-list class="list-position">
        <v-img
        src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/Star_Trek_logo_ultimate.jpg?sign=cb2ec1dd19033223b18dc9c7450daa11&t=1554029097"
      />    
      
        

 <hr class="mt-3 v-divider theme--light" >

        
            <v-list-tile
              avatar
              ripple
               @click="selectPath('starTerkPreface')"
              class="v-list__tile--avatar"
            >
              
                  <v-icon class="v-icon-position">details</v-icon>
                  <v-list-tile-title  class="v-title-position"></v-list-tile-title>
             
            </v-list-tile>
       

      <v-list-group
        prepend-icon="account_circle"
       
      >
        <template v-slot:activator>
          <v-list-tile>
            <v-list-tile-title></v-list-tile-title>
          </v-list-tile>
        </template>
        
          <v-list-tile
         
             v-for="item in itemsHero"
            :key="item.title"      
            ripple
            @click="selectPath(item.name)"
          >
            
                  <v-list-tile-avatar size="36" class="avatar-position ">
                    <v-img  :src="item.avatar" />
                  </v-list-tile-avatar>
             
                  <v-list-tile-content class="content-position-starTerk">
                    <v-list-tile-title  v-text="item.title"></v-list-tile-title>
                  </v-list-tile-content>
            
         </v-list-tile>   
      
      </v-list-group>

      

      
    
    
          <v-list-tile
            avatar
            ripple
            @click="selectPath('starTerkMovie')"
            class="v-list__tile--avatar" 
          >
            
            <v-icon class="v-icon-position">local_movies</v-icon>
            <v-list-tile-title  class="v-title-position"></v-list-tile-title>
          </v-list-tile>
      
     
          <v-list-tile
            avatar
            ripple
            @click="selectPath('starTerkTV')"
            class="v-list__tile--avatar"
          >
            <v-icon class="v-icon-position">tv</v-icon>
            <v-list-tile-title  class="v-title-position"></v-list-tile-title>
          </v-list-tile>
      
       
      </v-list>


    </v-navigation-drawer>


    <v-navigation-drawer permanent
      absolute
      overflow
      app
      right
      width="200"
    >
    
    </v-navigation-drawer>

    <v-toolbar clipped-left
              clipped-right
              app 
              dark>
   

   <a class="logo" href="/" style="display:flex;flex-direction:row">
        <img 
        width=50
        height=50
        src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/img/webLogo.png?sign=94864dc9020544a49ab2194e9c79be13&t=1555404548"/>
        <v-toolbar-title style="margin-left:10px;margin-top:10px;"></v-toolbar-title>
    </a>
    <v-spacer></v-spacer>
      <v-text-field
        flat
        solo-inverted
        hide-details
        prepend-inner-icon="search"
        label=""
        class="hidden-sm-and-down"
      ></v-text-field>
<v-spacer></v-spacer>

         <v-toolbar-items 
         class="hidden-sm-and-down"
          v-for="bar in itemsBar"
          :key="bar.title"    
         
         >

      <v-btn flat @click="selectBar(bar.name)">{{bar.title}}</v-btn>

    </v-toolbar-items>

    </v-toolbar>

    
    <v-content>
      <v-container fluid> 
           <router-view></router-view>
      </v-container>
    </v-content>
      
    <v-footer :inset="footer.inset" app dark>
      <span class="px-3">&copy; {{ new Date().getFullYear() }}</span>
    </v-footer>

    
  </v-app>

</template>

<script>

  export default {
    
    data: () => ({
      itemsHero: [
          { name:'kirk', title: '',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/kirk.jpg?sign=d4cd787c6d8f168624fa8fd13c76c8cb&t=1555340542' },
          { name:'spock',title: '',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/spock.jpg?sign=d98d8a41ac132391d14fa4de8aebb7af&t=1555340571' },
          { name:'mcCoy',title: '',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/mcCoy.jpg?sign=ed72939210b34a88560b509f347f758e&t=1555340559' },
          { name:'scotty',title: '',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/scotty.jpg?sign=18b07f7a569544b78ffd12e08de2f144&t=1555340617' },
          { name:'uhura',title: '',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/uhura.jpg?sign=bc1b55fd226199c59dbbc578a7f6b0b8&t=1555340600' },
          { name:'sulu', title: '',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/sulu.jpg?sign=64dc0436c6f541d6d0b7b1f9acb9acca&t=1555340582' },
          { name:'chekov',title: '',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/chekov.jpg?sign=c5b36a160bbe523b0bbb9a58b8d1c3a9&t=1555340516' },
          { name:'khan',title: '',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/khan.jpg?sign=ba4a17e25e5b9347b8fca10b9fe3dae5&t=1555340527' },
        ],

  
        itemsBar:[
          { name:'marvel/marvelPreface',title:''},
          { name:'DC/DCPreface',title:'DC'},
          { name:'starWar/starWarPreface',title:''},
          { name:'starTerk/starTerkPreface',title:''},
        ],
        
        
      footer: {
        inset: false
      },

     
    }),

    methods:{
      selectPath(name){ 
        var clickName=null;
         var _this=this;
        _this.clickName=name;
        _this.$router.push({path:'/starTerk/'+name});
      },
     selectBar(name){ 
        var clickName=null;
         var _this=this;
        _this.clickName=name;
        _this.$router.push({path:'/'+name});
      },
    }
 
  
     
    
    
    
  }
</script>

<style>

.v-title-position{
  margin-left: 32px;
}
.v-icon-position{
  margin-left: 0px;
}
.list-position{
  margin-top: 60px;
}
.content-position-starTerk{
  font-size: 20px;
  margin-left: 10px;
}
.item-postition{
  margin-top:0px;
}
.v-list__tile--avatar{
  height: 48px !important;
}

</style>
